.customer-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-left: 40px;
  padding: 0;
}

.customer-item {
  padding: 20px;
  background-color: white;
  margin-bottom: 20px;
  border-radius: 10px;
  box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%),
    0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
}

.account-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  padding: 0;
}

.account-item {
  display: inline-flex;
  justify-content: space-between;
  align-items: flex-end;
}

.section-title {
  font-size: 26px;
  font-weight: 600;
  padding: 20px 0 0 40px;
}
